<template>
	<section>
		<div class="panel">
			<div class="title">
				<span></span>
				<div class="pull-right">
					<el-button type="success" @click="onSubmit('form')" class="el-button-shadow"></el-button>
					<el-button type="danger" @click="resetForm('form')" class="el-button-shadow"></el-button>
				</div>
			</div>
			<div class="content">
				<el-form ref="form" :model="form" :rules="rules" label-width="110px" :inline-message="isInlineMessage" @submit.prevent="onSubmit">
					<el-form-item label="" prop="name">
						<el-input v-model="form.name"></el-input>
					</el-form-item>
					<el-form-item label="" prop="test"> 
						<el-select v-model="form.test" placeholder="">
							<el-option label="1" value="1"></el-option>
							<el-option label="2" value="2"></el-option>
							<el-option label="2" value="3"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="grade">
						<el-select v-model="form.grade" placeholder="">
							<el-option label="16" value="16"></el-option>
							<el-option label="15" value="15"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="department">
						<el-select v-model="form.department" placeholder="">
							<el-option label="" value="wuli"></el-option>
							<el-option label="" value="jisuanji"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="class">
						<el-select v-model="form.class" placeholder="">
							<el-option label="1" value="1"></el-option>
							<el-option label="2" value="2"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="listeners">
						<el-select v-model="form.listeners" placeholder="">
							<el-option label="" value="zhang"></el-option>
							<el-option label="" value="li"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="begintime">
						<el-date-picker type="datetime" placeholder="" format="yyyy/MM/dd HH:mm" v-model="form.begintime" style="width: 240px;"></el-date-picker>
					</el-form-item>
					<el-form-item label="" prop="endtime">
						<el-date-picker type="datetime" placeholder="" format="yyyy/MM/dd HH:mm" v-model="form.endtime" style="width: 240px;"></el-date-picker>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</section>
</template>
<script>
	export default {
		data() {
			return {
				isInlineMessage: true,
				form: {
					name: '',
					test: '',
					grade: '',
					department: '',
					class: '',
					listeners: '',
					begintime: '',
					endtime: ''
				},
				rules:{
					name:[
						{required: true, message: '', trigger: 'blur'},
						{min: 2, max: 30, message: '2-30', trigger: 'blur'}
					],
					test:[
						{required: true, message: '', trigger:'change'}
					],
					grade: [
						{required: true, message: '', trigger:'change'}
					],
					department: [
						{required: true, message: '', trigger:'change'}
					],
					class: [
						{required: true, message: '', trigger:'change'}
					],
					listeners:{required:true, message: '', trigger:'change'},
					begintime:{required:true, message: '', trigger:'change'},
					endtime:{required:true, message: '', trigger:'change'}
				}
			}
		},
		computed:{
			
		},
		methods: {
			onSubmit(formName, flag) {
				this.$refs[formName].validate((isValid) => {
					if(isValid){
						//to do
						//save ...
						
					}else{
						return false;
					}
				});
			},
			resetForm(formName){
				this.$refs[formName].resetFields();
			},
		}
	}
</script>
<style scoped lang="scss">
    @import '~scss_vars';
    
</style>


