import Mock from 'mockjs';
import _ from 'lodash';

const _selectArr = [
    {
        id: '0',
        name: '_0'
    },
    {
        id: '1',
        name: '_1'
    },
    {
        id: '2',
        name: '_2'
    },
];
const TypeList = [];
const UsageList = [];
const GroupList = [];
const ChapterList = [];
const SubjectList = [];
const QuestionList = [];
const TagList = [];
const CourseList = [];

for (let i = 0; i < 3; i++) {
    TypeList.push(Mock.mock({
        id: i,
        name: `_${i}`,
    }));
}
for (let i = 0; i < 3; i++) {
    UsageList.push(Mock.mock({
        id: i,
        name: `_${i}`,
    }));
}
for (let i = 0; i < 3; i++) {
    SubjectList.push(Mock.mock({
        id: i,
        name: `_${i}`,
    }));
}
for (let i = 0; i < 3; i++) {
    ChapterList.push(Mock.mock({
        id: i,
        name: `_${i}`,
        subject: '',
        creator: 'admin',
        desc: '',
    }));
}
for (let i = 0; i < 86; i++) {
    QuestionList.push(Mock.mock({
        id: Mock.Random.id(),
        name: '',
        type: '',
        subject: '',
        chapter: '',
        creator: 'admin',
        same: '01',
    }));
}
for (let i = 0; i < 20; i++) {
    const arr = _.slice(QuestionList, i * 3, i * 3 + 4);
    GroupList.push(Mock.mock({
        id: i,
        name: `_${i}`,
        subject: '',
        chapter: '',
        children: arr,
    }));
}
for (let i = 0; i < 86; i++) {
    TagList.push(Mock.mock({
        id: i,
        name: `${i}`,
        creator: 'admin',
        desc: '',
    }));
}
for (let i = 0; i < 86; i++) {
    CourseList.push(Mock.mock({
        id: i,
        name: `${i}`,
        subject: '',
        grade: '14',
        creator: 'admin',
    }));
}

export {
    TypeList,
    UsageList,
    SubjectList,
    ChapterList,
    GroupList,
    QuestionList,
    CourseList,
    TagList,
};



