<template>
	<section>
		<my-filter :list="filterList" @callback="search"></my-filter>
        <div class="panel">
            <div class="title">
                <el-input placeholder="" v-model="searchkey">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
                <div class="pageArea">
                    <Page :current="currentPage" :total="total" :pageSize="pageSize" @page-change="pageChange"></Page>
                </div>
                
            </div>
            <div class="content">
                <el-table :data="papers" highlight-current-row v-loading="listLoading" style="width: 100%;">
                    <el-table-column type="index" label="" width="60">
                    </el-table-column>
                    <el-table-column prop="name" label="" min-width="160">
                        <template scope="scope">
                            <router-link to="/">{{scope.row.name}}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column prop="time" label="(min)" min-width="100">
                    </el-table-column>
                    <el-table-column prop="project" label="" min-width="120">
                    </el-table-column>
                    <el-table-column prop="category" label="" min-width="100">
                    </el-table-column>
                    <el-table-column prop="status" label="" min-width="100" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column prop="creator" label="" min-width="100">
                    </el-table-column>
                    <el-table-column label="" width="150">
                        <template scope="scope">
                            <el-button type="primary" @click="handleEdit(scope.$index, scope.row)" :disabled="scope.row.status!='0'"></el-button>
                            <el-button type="danger" @click="handleDel(scope.$index, scope.row)" :disabled="scope.row.status!='0'"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
	</section>
</template>
<script>
	import myFilter from '../../common/myFilter.vue'
    import Pagination from '../../common/Pagination.vue'
    export default {
        components:{
        	myFilter,
        	Page:Pagination
        },
        data() {
            return {
                searchkey:'',
                filterList:[{
                    title:'',
                    field:'project',
                    children:[{
                        value:'hysics',
                        text:''
                    },{
                        value:'mathematics',
                        text:''
                    },{
                        value:'english',
                        text:''
                    }]
                },{
                    title:'',
                    field:'category',
                    children:[{
                        value:'random',
                        text:''
                    },{
                        value:'manual',
                        text:''
                    }]
                },{
                    title:'',
                    field:'status',
                    children:[{
                        value:'doing',
                        text:''
                    },{
                        value:'done',
                        text:''
                    }]
                }],
                papers: [{
                    name:'',
                    time:'60',
                    project:'',
                    category:'',
                    status:'0',
                    creator:'admin'
                },{
                    name:'',
                    time:'60',
                    project:'',
                    category:'',
                    status:'1',
                    creator:'admin'
                },{
                    name:'',
                    time:'60',
                    project:'',
                    category:'',
                    status:'2',
                    creator:'admin'
                }],
                total: 10,
                currentPage: 1,
                pageSize:5,
                listLoading: false
            }
        },
        methods: {
            search(value){
                console.log('search',value);
            },
            pageChange(){

            },
            formatStatus(row, column){
                if(row.status == '0'){
                    return '';
                }else if(row.status == '1'){
                    return ''
                }else if(row.status == '2'){
                    return ''
                }else{
                    return ''
                }
            }
           
        },
        mounted() {

        }
    }
</script>
<style scoped lang="scss">
    @import '~scss_vars';
    .filter-wrap{
        margin-bottom: 20px;
    }
</style>



