<template>
	<section>
		<div v-show="!isNext" class="panel">
			<div class="title">
				<span></span>
				<div class="pull-right">
					<el-button type="success" @click="onSubmit('form')" class="el-button-shadow"></el-button>
					<el-button type="danger" @click="resetForm('form')" class="el-button-shadow"></el-button>
				</div>
			</div>
			<div class="content">
				<el-form id="paperForm" ref="form" :model="form" :rules="rules" label-width="120px" :inline-message="isInlineMessage" @submit.prevent="onSubmit">
					<el-form-item label="" prop="name">
						<el-input v-model="form.name"></el-input>
					</el-form-item>
					<el-form-item label="" prop="subject"> 
						<el-select v-model="form.subject" placeholder="">
							<el-option label="" value="hysics"></el-option>
							<el-option label="" value="mathematics"></el-option>
							<el-option label="" value="english"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="mode">
						<el-select v-model="form.mode" placeholder="">
							<el-option label="" value="random"></el-option>
							<el-option label="" value="manual"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="time">
						<el-input v-model="form.time"></el-input>
						<span class="text-primary" style="margin-left:12px;">*</span>
					</el-form-item>
					<el-form-item label="" :required="isConfigRequired" v-if="isConfigRequired">
						<el-col :span="24">
							<el-form-item class="form-item-unit" prop="radiocount">
								 <el-input v-model.integer="form.radiocount"></el-input> 
							</el-form-item>
							<el-form-item class="form-item-unit" prop="radioscore">
								 <el-input v-model.integer="form.radioscore"></el-input> 
							</el-form-item>
						</el-col>
						<el-col :span="24">
							<el-form-item class="form-item-unit" prop="checkcount">
								 <el-input v-model.integer="form.checkcount"></el-input> 
							</el-form-item>
							<el-form-item class="form-item-unit" prop="checkscore">
								 <el-input v-model.integer="form.checkscore"></el-input> 
							</el-form-item>
						</el-col>
						<el-col :span="24">
							<el-form-item class="form-item-unit" prop="judgecount">
								 <el-input v-model.integer="form.judgecount"></el-input> 
							</el-form-item>
							<el-form-item class="form-item-unit" prop="judgescore">
								 <el-input v-model.integer="form.judgescore"></el-input> 
							</el-form-item>
						</el-col>
						<el-col :span="24">
							<el-form-item class="form-item-unit" prop="optional">
								 <el-input v-model.integer="form.optional"></el-input> 
							</el-form-item>
							<el-form-item class="form-item-unit" prop="necessary">
								 <el-input v-model.integer="form.necessary"></el-input> 
							</el-form-item>
							<el-form-item class="form-item-unit" prop="choosescore">
								 <el-input v-model.integer="form.choosescore"></el-input> 
							</el-form-item>
						</el-col>
					</el-form-item>
					<el-form-item label="" required>
						<el-input v-model="form.total" disabled></el-input>
					</el-form-item>
					<el-form-item v-if="!isConfigRequired">
						<el-button type="success" class="el-button-shadow" @click="onSubmit('form','next')"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
		<Detail v-if="isNext" @back="detailBack"></Detail>
	</section>
</template>
<script>
	import Detail from './Detail.vue'
	export default {
		components:{
			Detail
		},
		data() {
			var integerPattern = '^\\d+$';//>=0
			var isRequiedValidator = (rule, value, callback) => {//
				if(this.isConfigRequired && value.length == 0){
					callback(new Error(''));
				}else{
					callback();
				}
			};
			return {
				isInlineMessage: true,
				isNext: false,
				form: {
					name: '',
					subject: '',
					mode: 'random',
					time: '',
					radiocount:'',
					radioscore:'',
					checkcount:'',
					checkscore:'',
					judgecount:'',
					judgescore:'',
					optional:'',
					necessary:'',
					choosescore:'',
					total: '100'
				},
				rules:{
					name:[
						{required: true, message: '', trigger: 'blur'},
						{min: 2, max: 30, message: '2-30', trigger: 'blur'}
					],
					subject:[
						{required: true, message: '', trigger:'change'}
					],
					mode: [
						{required: true, message: '', trigger:'change'}
					],
					time: [
						{required: true, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					radiocount: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger: 'change'}
					],
					radioscore: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					checkcount: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					checkscore: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					judgecount: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					judgescore: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					optional: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					necessary: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					choosescore: [
						{validator: isRequiedValidator, message: '', trigger:'change'},
						{pattern: integerPattern, message: '', trigger:'change'}
					],
					total:{required:true}
				}
			}
		},
		computed:{
			isConfigRequired(){
				return this.form.mode === 'random';
			}
		},
		methods: {
			onSubmit(formName, flag) {
				this.$refs[formName].validate((isValid) => {
					if(isValid){
						//to do
						//save ...
						if(flag && flag == 'next'){
							this.isNext = true;
						}
					}else{
						return false;
					}
				});
			},
			resetForm(formName){
				this.$refs[formName].resetFields();
			},
			detailBack(){
				this.isNext = false;
			}
		}
	}
</script>
<style scoped lang="scss">
    @import '~scss_vars';
</style>


